
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}

body {
  background: grey;
}
.chat-demo {
  width: 25em;
  margin: auto;
  background: white;
}
.main-title {
  padding: 1em;
  margin: 0;
}
#logged-in-as {
  text-align: right;
  float: right;
  font-size: .9em;
  padding: .3em .5em;
}











































.message-view {
  margin: 1px;
  border: thin lightblue solid;
}
.msg-header {
  font-size: .9em;
  height: 1em;
  color: saddlebrown;
}
.stamp {
  float: right;
}
.msg-body {
  padding: 1em;
}


#compose {
  position: relative;
  height: 4em;
}
#compose * {
  position: absolute;
  top: 1px;
  bottom: 1px;
}
#compose button {
  right: 1px;
  width: 12%;
}
#compose textarea {
  left: 1px;
  width: 85%;
}



































